
#mainListing{
    margin-left: 4%;
    margin-top: 6%;
    overflow-x: hidden;
}

#filter{
    width:21%;
    height:100%;
    float: left;
    border: 5px shadow black;
    box-shadow: 0px 0px 4px gray;
    padding: 10px;
    margin-top: 5%;
    overflow-y: auto;
    position: fixed;
}
#filter:hover{
    background-color:lightgoldenrodyellow
}

#content{
    width:70%;
    float: left;
    overflow-x: hidden;
    margin-left: 250px;
    margin-top: 10%;

}

.list_content{
    width:100%;
    height: 200px;
    box-shadow: 0px 0px 4px gray;
    margin-top: 2%;
    
}
.item:hover{
    background-color:lightgoldenrodyellow;
    /*transform: scale(1.1);*/

}
.item {
    width: 685px;
    height: 250px;
    box-shadow: 0 5px 10px 0 gray;
    margin-bottom: 20px;
}


.Image {
    width: 190px;
    height: 200px;
    border-radius: 15px;
    background-color: #ea6060;
    margin-left:5%;
    margin-top: 20px ;
}



.hotel_name {
    width: 100%;
    margin-left:-10%;
    height: 43px;
    font-family: Poppins;
    font-size: 19px;
    font-weight: 600;
    font-stretch: normal;
    font-style: normal;
    line-height: 2;
    letter-spacing: normal;
    text-align: left;
    color: #192f60;
}
.hotel_name :hover{
    color: blueviolet;
}
.city_name {
    width: 100%;
    height: 23px;
    font-family: Poppins;
    font-size: 14px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: 2;
    letter-spacing: normal;
    text-align: left;
    color: #192f60;
}
.city_name:hover{
    color: red;
}
.rating {
    line-height: 11;
}
.cost {
    line-height: 5;
}


@media screen and (max-width:1000px){
    body{
        background-color: lightslategrey;
        
    }
}

@media screen and (max-width:800px){
    body{
        background-color: lightpink;
    }
   
}

@media screen and (max-width:600px){
    body{
        background-color: lightgreen;
    }
    
}


